import React from 'react';
import store from 'store';
import { useTranslation } from 'react-i18next';
import { Helmet } from 'react-helmet';

import MainSlider from "./MainSlider";
import MainBlog from './MainBlog/index';
import MainSwiper from "./components/MainSwiper";
import ConstructorSlider from '../../components/ConstructorSlider';
import SetsSlider from '../../components/SetsSlider';
import MainWhyUs from "./MainWhyUs";
import MainFeedback from "./MainFeedback";
import MainHeader from "./components/MainHeader";
import MainBottomBlock from "./MainBottomBlock";
import ShareDiv from './components/ShareDiv/ShareDiv';

import './index.scss';
import logo from '../../assets/components/Wrapper/Header/logo.webp';

const MainPage = (props) => {
    const { t } = useTranslation();

    const createJSONLD = () => {
        if (props.categories) {
            const date = {
                '@context': 'https://schema.org/',
                "@type": "Offer",
                'offeredBy': {
                    '@type': 'Organization',
                    'name': '24ROLLS',
                    'address': 'Днепр, Запорожье',
                    'location': 'Днепр, Запорожье'
                },
                'seller': {
                    '@type': 'Organization',
                    'name': '24ROLLS',
                    'address': 'Днепр, Запорожье',
                    'location': 'Днепр, Запорожье'
                },
                'category': props.categories.map(el => el.name_ru).join(', '),
                'priceCurrency': 'UAH'
            };

            return JSON.stringify(date);
        }
    };

    const cityName = store.get('24rolls') ? store.get('24rolls').customOptions.city : 'Dnipro';
    let city;
    if (cityName === 'Zaporijja') {
        city = 'Zp'
    } else if (cityName === 'Dnipro') {
        city = 'Dp'
    } else {
        city = 'Kh'
    };

    const {
        text,
        shares,
        rollGoods,
        sliders,
        sharesSlider,
        blogSlider
    } = props;

    return (
        <>  
            <Helmet>
                <title>
                    {props.seo ? props.seo.title : '24ROLLS'}
                </title>
                <meta name="description" content={props.seo && props.seo.description} />
                <meta name="keywords" content={props.seo && props.seo.keywords} />
                <script async type="application/ld+json">
                    {createJSONLD()}
                </script>
                <script type="application/ld+json">
                    {JSON.stringify({ "@context": "http://schema.org", "@type": "WebSite", "@id": "#website", "url": "https://24rolls.com.ua/", "name": "\u0421\u0443\u0448\u0438 24Rolls Dnepr", "potentialAction": { "@type": "SearchAction", "target": "https://24rolls.com.ua/?s={search_term_string}", "query-input": "required name=search_term_string" } })}
                </script>
                <script type="application/ld+json">
                    {JSON.stringify({ "@context": "http://schema.org", "@type": "Organization", "url": "https://24rolls.com.ua/", "logo": logo, "contactPoint": [{ "@type": "ContactPoint", "telephone": "068 719 24 24", "contactType": ["customer service", "sales"], "areaServed": ["UA"], "contactOption": ["TollFree"], "availableLanguage": ["Russian", "Ukrainian"] }], "sameAs": ["https://www.youtube.com/channel/UCmJwKEidDraJtVdYtpnsJSA", "https://www.instagram.com/24rolls.com.ua/"] })}
                </script>
            </Helmet>

            <div className={'main'}>
                <MainSlider {...props} slider={sharesSlider} />
                <MainSwiper 
                    {...props} 
                    slider={sliders.filter(el => el.name.includes(`1(${city})`))[0]} 
                    more={`Еще больше в этой категории >>`}
                />
                <MainHeader title={t('main.title.1')} h={'h1'} />
                <SetsSlider {...props} list={rollGoods} title={false} />
                <ConstructorSlider {...props} items={rollGoods} />
                {shares[0] && <ShareDiv src={shares[0].image} />}
                <MainSwiper 
                    {...props} 
                    slider={sliders.filter(el => el.name.includes(`2(${city})`))[0]}
                    more={`Еще больше в этой категории >>`}
                />
                {shares[1] && <ShareDiv src={shares[1].image} />}
                <MainSwiper 
                    {...props} 
                    slider={sliders.filter(el => el.name.includes(`3(${city})`))[0]} 
                    more={`Еще больше в этой категории >>`}
                />
                {shares[2] && <ShareDiv src={shares[2].image} />}
                <MainSwiper 
                    {...props} 
                    slider={sliders.filter(el => el.name.includes(`4(${city})`))[0]} 
                    more={`Еще больше в этой категории >>`}
                />
                {shares[3] && <ShareDiv src={shares[3].image} />}
                <MainBlog slider={blogSlider} {...props} />
                <MainHeader title={t('main.title.3')} />
                <MainWhyUs />
                <MainFeedback />
                <MainHeader title={t('main.title.5')} />
                {text && <MainBottomBlock text={text} />}
            </div>
        </>
    )
}

export default MainPage;